.activity-feed {
  background: #fff;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
}

.activity-header {
  margin-bottom: 16px;
}

.activity-header h3 {
  font-size: 20px;
  font-weight: 600;
  margin: 0;
  color: #333;
}

.activity-header p {
  font-size: 14px;
  color: #888;
  margin-top: 4px;
}

.activity-chart {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 120px;
}

.activity-day {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bars {
  display: flex;
  gap: 4px;
  align-items: flex-end;
  height: 100px;
}

.bar {
  width: 6px;
  border-radius: 4px;
}

.bar-1 {
  background-color: #30c6ff;
}

.bar-2 {
  background-color: #6c63ff; 
}

.day-label {
  margin-top: 8px;
  font-size: 12px;
  color: #555;
}
