/*  GLOBAL   */
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background: white;
  color: #333;
}

.dashboard-container {
  display: flex;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}
.dashboard-overview{
  flex-direction: column;
}


.sidebar {
  width: 220px;
  background: f4f9ff;

  padding: 30px 20px;
 
  display: flex;
  flex-direction: column;

}

/*  MAIN */
.main-content {
  flex: 1;
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}

/* ========== HEADER ========== */
.dashboard-overview{
  margin: 15px;
  padding: 10px;
}
.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.03);
  margin-bottom: 20px;
}

/* ========== OVERVIEW + CALENDAR WRAPPER ========== */
.dashboard-sections {
  display: flex;
  gap: 20px;
  flex: 1;
}
.anatomy-section{
  display: flex;
  margin-right: 15px;
  

}
.anatomy-section img{
  height: 100%;
  width: 100%;
  background-color: rgb(244 249 255);
}
.indicator-issues {
    padding: 30px 60px;
    margin: 10px;
    background: #f4f9ff;
    border-radius: 10px;
    border: 5px solid #ffffff;
}
.indicator-issue{
  display: flex;
  flex-direction: column;
}

.appointment-details {
    display: flex;
    gap: 10px;
    margin-top: 15px;
}

/* ========== OVERVIEW AREA (LEFT SIDE) ========== */
.overview-area {
  flex: 2;
  background: #fff;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.03);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* ========== CALENDAR AREA (RIGHT SIDE) ========== */
.calendar-area {
  flex: 1.5;
  background: #fff;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.03);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* ========== CARDS & SECTIONS ========== */
.card {
  background: #f7faff;
  border-radius: 16px;
  padding: 16px;
  margin-bottom: 15px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.02);
}

/* ========== IMAGE STYLING ========== */
.body-image-container {
    height: 600px;
    width: 425px;
      position: relative;
}
.anatomy-image {
  width: 100%;
  height: 100%;
  margin: auto;
  display: block;

}
span.indicator.heart {
    position: absolute;
    top: 20%;
    right: 10%;
    background: red;
    color: white;
    border-radius: 10px;
    padding: 10px;
}


.status-badge {
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  color: white;
  display: inline-block;
  margin-right: 5px;
}

.status-green {
  background-color: #4cd964;
}
.status-red {
  background-color: #ff3b30;
}
.status-blue {
  background-color: #007aff;
}

/* ========== CALENDAR ELEMENTS ========== */
.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
  margin-top: 10px;
  font-size: 14px;
  text-align: center;
}

.calendar-day {
  padding: 10px;
  background: #eef4ff;
  border-radius: 12px;
  font-weight: 500;
}

.appointment-block {
  background: #e0f0ff;
  border-radius: 10px;
  padding: 6px;
  margin-top: 5px;
  font-size: 12px;
}

/*UPCOMING APPOINTMENTS */
.upcoming-schedule {
  margin-top: 20px;
}

.schedule-day {
  font-weight: 600;
  margin-bottom: 10px;
}

.appointment-card {
  background: #b5cfe5;
  padding: 12px 16px;
  border-radius: 14px;
  margin-bottom: 10px;
 
  justify-content: space-between;
  align-items: center;
}
.appointment-details :first-child{
  background-color: blue;
  color:white;
}

/*  ACTIVITY  PLACEHOLDER  */
.activity-feed {
  margin-top: 20px;
}

.bar-chart {
  display: flex;
  align-items: flex-end;
  gap: 8px;
  height: 100px;
}

.bar {
  width: 6px;
  background: #007aff;
  border-radius: 4px;
}

.bar.small {
  height: 30%;
}
.bar.medium {
  height: 60%;
}
.bar.large {
  height: 90%;
}


.dashboard-container {
  display: flex;
  padding: 20px;
  gap: 30px;
  background-color: #f3f7fd;
  font-family: 'Segoe UI', sans-serif;
}

.dashboard-left {
  width: 60%;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.dashboard-right {
  width: 40%;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.health-card {
  background: #f4f9ff;

  width: 120px;
  height: max-content;
  padding: 20px 10px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  margin-bottom: 16px;
  margin-left: 16px;
}

.health-card h4 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
}

.health-card p {
  margin: 4px 0 10px;
  font-size: 12px;
  color: #999;
}

.status-bar {
  width: 100%;
  height: 8px;
  background: #e0e0e0;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
  justify-content: center;
  align-items: center;
}


.status-fill {
  height: 100%;
  border-radius: 4px;
  transition: width 0.3s ease;
}
.health-name{
  width: 50px;
  height: 50px;
}
.health-name img{
  width: 100%;
  height: 100%;
}


.upcoming {
  background: #fff;
  padding: 20px;
  border-radius: 16px;
  margin-bottom: 20px;
}

.appointments {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.appt-card {
  background: #e1f5fe;
  padding: 10px 15px;
  border-radius: 10px;
  min-width: 150px;
}

.appt-card span {
  font-size: 18px;
  margin-right: 6px;
}

.appt-card p {
  margin: 0;
  font-weight: 500;
}

.appt-card small {
  color: #555;
}



@media (max-width: 1024px) {
  .dashboard-container {
    flex-direction: column;
    padding: 10px;
  }

  .dashboard-left,
  .dashboard-right {
    width: 100%;
  }

  .dashboard-sections {
    flex-direction: column;
  }

  .calendar-grid {
    grid-template-columns: repeat(4, 1fr);
  }

  .body-image-container {
    height: 400px;
    width: 100%;
  }

  /* .sidebar {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    padding: 10px;
  } */
}

/* Mobile Devices */
@media (max-width: 768px) {
  .dashboard-header {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px;
  }
  .dashboard-main-content{
    flex-wrap: wrap;
  }
  .anatomy-section{
    flex-wrap: wrap;
  }

  .calendar-grid {
    grid-template-columns: repeat(3, 1fr);
    font-size: 12px;
  }

  .appointment-card {
    flex-direction: column;
    align-items: flex-start;
  }

  .health-card {
    width: 100%;
    margin: 10px 0;
  }

  .dashboard-sections {
    flex-direction: column;
  }

  .body-image-container {
    height: 300px;
  }

  .bar-chart {
    height: 80px;
  }

  .bar {
    width: 5px;
  }

  .main-content {
    margin-left: 0;
  }

  .overview-area,
  .calendar-area {
    padding: 10px;
  }

  .appointments {
    flex-direction: column;
  }
}