.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  background-color: #f4f9ff;
  padding: 10px 20px;
  gap: 1rem;
}

.top-bar-left {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  align-items: center;
  gap: 1.5rem;
}

.top-bar-right {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.bar-logo {
  font-size: 22px;
  color: green;
  background-color: #f4f9ff;
}

.search-bar-notification {
  width: 100%;
  max-width: 320px;
  margin-left: 0;
}

input.search-bar {
  width: 100%;
  max-width: 320px;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.search-bar svg {
  width: 24px;
  height: 24px;
  position: relative;
  top: 6px;
}

.user-profile svg {
  width: 40px;
  height: 35px;
}

button.add-btn {
  margin-left: 10px;
  font-size: 16px;
  font-weight: bold;
  padding: 8px 14px;
  border: none;
  border-radius: 10px;
  background-color: blue;
  color: white;
  cursor: pointer;
}

.icons {
  display: flex;
  gap: 10px;
  align-items: center;
}

/* âœ… Media Queries for responsiveness */
@media (max-width: 768px) {
  .top-bar {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
  }

  .top-bar-left,
  .top-bar-right {
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  input.search-bar {
    width: 100%;
    max-width: none;
  }

  .bar-logo {
    font-size: 18px;
  }

  button.add-btn {
    width: 100%;
    margin-left: 0;
  }

  .search-bar-notification {
    width: 100%;
    margin-left: 0;
  }

  .icons {
    justify-content: flex-start;
  }
}
